'use strict';

class GGBView {
	constructor(){
		this._container = document.querySelector('.app-container');
		this._WIDTH = _container.getBoundingClientRect().width;
		this._HEIGHT = _container.getBoundingClientRect().height;
		this._params = {
			'appName': 'graphing',
			'width': (Math.round(_WIDTH)),
			'height': (_HEIGHT - 45),
			'showToolBar': true,
			'showAlgebraInput': true,
			'showMenuBar': true,
		}

		this._applet = new GGBApplet(_params, true);
	}

	renderGGB(){
		this._applet.inject(element);
	}
}

export default new GGBView();